<template>
    <div class="page-loader" v-if="!isloaded">

        <div class="cube-loader">
            <div class="cube-top"></div>
            <div class="cube-wrapper">
                <span style="--i:0" class="cube-span"></span>
                <span style="--i:1" class="cube-span"></span>
                <span style="--i:2" class="cube-span"></span>
                <span style="--i:3" class="cube-span"></span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isloaded: false
        }
    },
    mounted() {
        document.onreadystatechange = () => {
            if (document.readyState == "complete") {

                this.isloaded = true;
            }
        }
    }

}
</script>

<style>
.page-loader {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgb(6, 8, 31);
    z-index: 999;
}

.cube-loader {
    position: relative;


    /* u can choose any size */
    width: 75px;
    height: 75px;
    transform-style: preserve-3d;
    transform: rotateX(-30deg);
    animation: animate 4s linear infinite;
}

@keyframes animate {
    0% {
        transform: rotateX(-30deg) rotateY(0);
    }

    100% {
        transform: rotateX(-30deg) rotateY(360deg);
    }
}


.cube-loader .cube-wrapper {

    position: absolute;
    width: 100%;
    height: 100%;
    /* top: 0;
  left: 0; */
    transform-style: preserve-3d;
}

.cube-loader .cube-wrapper .cube-span {
    position: absolute;
    width: 100%;
    height: 100%;
    /* top: 0;
  left: 0; */
    /* width 75px / 2 = 37.5px */
    transform: rotateY(calc(90deg * var(--i))) translateZ(37.5px);
    background: linear-gradient(to bottom,
            hsl(225, 60%, 10%) 0%,
            hsl(225, 60%, 15%) 5.5%,
            hsl(225, 60%, 20%) 12.1%,
            hsl(225, 60%, 25%) 19.6%,
            hsl(225, 60%, 30%) 27.9%,
            hsl(225, 60%, 35%) 36.6%,
            hsl(225, 60%, 40%) 45.6%,
            hsl(225, 60%, 45%) 54.6%,
            hsl(225, 60%, 50%) 63.4%,
            hsl(225, 60%, 55%) 71.7%,
            hsl(225, 60%, 60%) 79.4%,
            hsl(225, 60%, 65%) 86.2%,
            hsl(225, 60%, 70%) 91.9%,
            hsl(225, 60%, 75%) 96.3%,
            hsl(225, 60%, 80%) 99%,
            hsl(225, 60%, 85%) 100%);
}

.cube-top {
    position: absolute;
    width: 75px;
    height: 75px;
    background: hsla(234, 39%, 35%, 0.955) 0%;
    /* width 75px / 2 = 37.5px */
    transform: rotateX(90deg) translateZ(37.5px);
    transform-style: preserve-3d;
}

.cube-top::before {
    content: '';
    position: absolute;
    /* u can choose any size */
    width: 75px;
    height: 75px;
    background: hsl(176.61, 42.28%, 40.7%) 19.6%;
    transform: translateZ(-90px);
    filter: blur(10px);
    box-shadow: 0 0 10px #323232,
        0 0 20px hsl(176.61, 42.28%, 40.7%) 19.6%,
        0 0 30px #323232,
        0 0 40px hsl(176.61, 42.28%, 40.7%) 19.6%;
}
</style>