<style>
* {
    box-sizing: border-box;
}

body {
    margin: 0px;
    font-family: 'segoe ui';
}

.nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 25px 150px;
    /* background-color: #4d4d4d00; */
    position: relative;



}

.nav>.nav-header {
    display: inline;
}

.nav>.nav-header>.nav-title {
