<template>
   <section id="contact">

      <h1 class="section-header">Contact</h1>
      <Transition name="fade">
         <div v-if="isFormInvalid.length" class=" alert alert-danger " role="alert">
            <ul>
               <li v-for="p in isFormInvalid" :key="p.id">{{ p }}</li>
            </ul>
         </div>
      </Transition>

      <Transition name="fade">
         <div v-if="thisvalid" class="alert alert-success" role="alert">
            <p>{{ thisvalid }}</p>
         </div>
      </Transition>

      <div class="contact-wrapper">

         <!-- Left contact page -->



         <form id="contact-form" class="form-horizontal" role="form" @submit.prevent="submithandel">



            <div class="form-group">
               <input type="text" v-model="form.namefill" class="form-control" id="name" placeholder="NAME" name="name"
                  value="">
            </div>

            <div class="form-group">
               <input type="email" class="form-control" v-model="form.emailfill" id="email" placeholder="EMAIL"
                  name="email" value="">
            </div>

            <div class="form-group">
               <!-- <input type="text" class="form-control" id="phone" placeholder="Phone#" name="phone" value="" required> -->
               <!-- this is phone vue fill and countrey  -->
               <vue-tel-input for="form.phonefill" class="form-label-countrey form-control " placeholder="Phone#"
                  v-model="form.phonefill" mode="international" maxlength="10"></vue-tel-input>
            </div>

            <textarea class="form-control" rows="10" placeholder="MESSAGE" name="message"
               v-model="form.comment"></textarea>


            <button class="send-button" @click="submitted" :disabled="submitted" id="submit" value="SEND" type="submit">
               SUBMIT
            </button>


         </form>

         <!-- Right contact page -->

         <div class="direct-contact-container">

            <ul class="contact-list">
               <li class="list-item"><i class="fa fa-map-marker fa-2x"><span
                        class="contact-text place">Lahore,Punjab,Pakistan</span></i></li>

               <li class="list-item"><i class="fa fa-phone fa-2x"><span class="contact-text phone"><a
                           href="tel:1-212-555-5555" title="Give me a call">(+92) 3174051144</a></span></i></li>

               <li class="list-item"><i class="fa fa-envelope fa-2x"><span class="contact-text gmail"><a
                           href="mailto:vilaayali89@gmail.com"
                           title="Send me an email">Vilaayali89@gmail.com</a></span></i>
               </li>

            </ul>

            <hr>

            <ul class="social-media-list">
               <li><a href="#" target="_blank" class="contact-icon">
                     <i class="fa fa-github" aria-hidden="true"></i></a>
               </li>
               <li><a href="#" target="_blank" class="contact-icon">
                     <i class="fa fa-codepen" aria-hidden="true"></i></a>
               </li>
               <li><a href="#" target="_blank" class="contact-icon">
                     <i class="fa fa-twitter" aria-hidden="true"></i></a>
               </li>
               <li><a href="#" target="_blank" class="contact-icon">
                     <i class="fa fa-instagram" aria-hidden="true"></i></a>
               </li>
            </ul>
            <hr>



         </div>

      </div>

   </section>
</template>

<script>

import { projectFireStore } from '../views/firebase/config'
import axios from 'axios';
import { Alert } from 'bootstrap';
import Cos2Btn from '../components/openbtn.vue';

export default {
   components: {
      Cos2Btn
   },
   data() {
      return {
         isFormInvalid: [],
         PhoneNumLength: 10,
         isEmpty: null,
         submitted: false,
         thisvalid: false,




         links: {
            phonenumber: 'https://api.whatsapp.com/send?phone=+923174051144',
            Email: 'mailto:vilaayali89@gmil.com',
            linkdin: 'https://www.linkedin.com/in/syedvilaayali'
         },

         form: {
            namefill: '',
            emailfill: '',
            phonefill: '',
            comment: '',

         },


      }
   },
   methods: {

      async submithandel() {


         let emailRegex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

         let name = this.form.namefill;
         let phone = this.form.phonefill.replace(/\s/g, '');
         let email = this.form.emailfill;
         let message = this.form.comment
         this.isFormInvalid = [];

         // Show Bootstrap validation alerts
         if (!name) {

            this.isFormInvalid.push('Name is required')
         }
         if (!email) {

            this.isFormInvalid.push('Email is required')
         }

         if (!emailRegex.test(this.form.emailfill)) {
            this.isFormInvalid.push('Please enter a valid email address');
         }

         if (!phone) {

            this.isFormInvalid.push('phone is required')
         }
         if (!message) {

            this.isFormInvalid.push('Message is required')
         }
         if (this.isFormInvalid.length === 0) {
            // Submit the form
            axios.post(`https://portfolio-new-805dd-default-rtdb.firebaseio.com/contact-forms.json`, this.form)
            // console.log(this.form.namefill, this.form.emailfill, this.form.phonefill);
            setTimeout(() => {
               this.thisvalid = 'Data Submitted Sucessesfully We Will be in touch soon. Thnaks for Contacting ';
            }, 1000);
            setTimeout(() => {
               this.thisvalid = false
            }, 10000);
            // Reset form fields
            this.form.namefill = '';
            this.form.emailfill = '';
            this.form.phonefill = '';
            this.form.comment = '';
            this.submitted = true;


         }



      }
   }
}

</script>

<style scoped>
#contact {
   width: 100%;
   height: 100%;
   padding: 20px;
   box-sizing: border-box;
}

.section-header {
   text-align: center;
   margin: 0;
   padding: 40px 0;
   font-size: 3rem;
   color: #fff;
   text-transform: uppercase;
   letter-spacing: 6px;
}

.contact-wrapper {
   display: flex;
   flex-direction: column;
   align-items: center;
   gap: 20px;
   max-width: 840px;
   margin: 0 auto;
}

.form-horizontal {
   max-width: 600px;
   width: 100%;

   padding: 20px;
   box-sizing: border-box;
}

.form-control,
textarea {
   width: 100%;
   background-color: #ffffff;
   color: #000000;
   border: 1px solid #777;
   padding: 10px;
   box-sizing: border-box;
   margin-bottom: 20px;
}

/* .send-button {
   width: 100%;
   padding: 10px;
   background-color: #333;
   color: #fff;
   border: none;
   cursor: pointer;
   transition: background-color 0.3s ease;
}

.send-button:hover {
   background-color: #555;
} */


button {
   width: 100%;
   border: none;
   color: #fff;
   background-image: linear-gradient(135deg, #49b3b0 0%, #12536c 31%, #290a47 100%);
   border-radius: 20px;
   font-family: inherit;
   font-size: 17px;
   padding: 0.6em 1.5em;
   transition: scale 0.2s ease;
}

button:disabled {
   opacity: 0.6;
   cursor: not-allowed;
   pointer-events: none;
}

button:hover {
   cursor: pointer;
   -webkit-animation: pulse 1.5s infinite;
   animation: pulse512 1.5s infinite;
}

button:active {
   scale: 0.95;
}

@keyframes pulse512 {
   0% {
      box-shadow: 0 0 0 0 #05bada66;
   }

   70% {
      box-shadow: 0 0 0 10px rgba(218, 03, 8, 0);
   }

   100% {
      box-shadow: 0 0 0 0 rgba(218, 03, 8, 0);
   }
}





.direct-contact-container {
   max-width: 400px;
   width: 100%;
   /* background-color: #000; */
   padding: 20px;
   box-sizing: border-box;
}

.contact-list {
   line-height: 3;
   list-style-type: none;
   padding: 0;
   margin: 0;
}

.list-item {
   margin-bottom: 10px;
   font-size: 1.2rem;
   color: #aaa;
}

.contact-text {
   font-size: 1.2rem;
   color: #ffffff;
}

.contact-text a {
   color: #ffffff;
   text-decoration: none;
}

.contact-text a:hover {
   color: #ffffff;
   text-decoration: underline;
}

.social-media-list {
   list-style-type: none;
   padding: 0;
   margin: 0;
   display: flex;
   justify-content: center;
   gap: 10px;
}

.social-media-list li {
   width: 40px;
   height: 40px;
   line-height: 40px;
   text-align: center;
   background-color: #e7e2e2;
   border-radius: 50%;
   cursor: pointer;
   transition: background-color 0.3s ease;
}

.social-media-list li:hover {
   background-color: #555;
}

.social-media-list li a {
   color: #fff;
   text-decoration: none;
}

.copyright {
   text-align: center;
   color: #555;
   font-size: 0.9rem;
}

hr {
   border-color: rgba(255, 255, 255, 0.6);
   margin: 20px 0;
}

@media screen and (min-width: 768px) {
   .contact-wrapper {
      flex-direction: coloum;
      justify-content: space-between;
   }

   .direct-contact-container {
      margin-left: 40px;
   }
}
</style>